.houseInterior {
	height: 250px;
  	width: 300px;
  	background-color: #ededed;
}

.First-room {
	height: 50px;
  width: 75px;
  	border-bottom: 1px thin #939292; 
  	border-right: 1px thin #939292;
}

.Second-room {
	height: 50px;
  width: 75px;
  	border-bottom: 1px thin #939292; 
  	border-left: 1px thin #939292;
}

.Light-Bulb{
	height: 15px;
  	width: 15px;
  	background-color: #555;
  	border-radius: 50%;
}